// src/adminAuthMiddleware.ts
import { NextRequest, NextResponse } from "next/server";
import { verifyToken, isAdminExist } from "@/utils/authUtils";
import jwt from 'jsonwebtoken';
import { jwtVerify } from 'jose';
import prisma from "@/lib/prisma";

const SECRET_KEY = process.env.JWT_SECRET || '';

export async function adminAuthMiddleware(req: NextRequest) {
    try {
        // Extract token from Authorization header
        const token = req.headers.get("authorization")?.split(" ")[1];
        if (!token) {
            return NextResponse.json({ error: "Unauthorized" }, { status: 401 });
        }

        // Verify token and extract admin details
        const { payload } = await jwtVerify(token, new TextEncoder().encode(SECRET_KEY));
        if (!payload || typeof payload !== 'object' || typeof payload.adminId !== 'number') {
            return NextResponse.json({ error: "Forbidden" }, { status: 403 });
        }
        console.log(`payload.adminId - `, payload.adminId);
        // Fetch admin details from database
        const admin = await prisma.admin.findUnique({
            where: { id: parseInt(payload.adminId, 10) },
            select: {
                id: true,
                name: true,
                email: true,
                password: true, // Hashed password stored in DB
                role: true,
            },
        });

        // If admin doesn't exist, return false with a message
        if (!admin) {
            return NextResponse.json({ error: `Admin Not Found 1: ${result.message}` }, { status: 404 });
        }

        // Clone the request and set custom headers
        const response = NextResponse.next();
        response.headers.set("x-admin-id", payload.adminId.toString());

        return response;
    } catch (error) {
        console.error(`error - `, error);
        return NextResponse.json({ error: "Internal Server Error" }, { status: 500 });
    }
}
